
html, body {
  height: 100%;
}

.list-wrapper {
  display: inline-block;
  /* width: 100%;
  height: 100%; */
}
.poke-list {
  position: relative;
}
.router-transition-enter-active,
.router-transition-leave-active {
  -webkit-transition: all .5s ease;
  transition: all .5s ease;
}
.router-transition-enter, .router-transition-leave-active {
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
}
.poke-cell {
  width: 120px;
  height: 120px;
  border: none;
  background-color: transparent;
  background-repeat: no-repeat;
  background-image: url(/static/img/sprites.6a39899.png);
}


































































.poke-detail-container {
  top: 50%;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: #FDFCFD;
  position: fixed;
}
.poke-detail-view {
  max-width: 50rem;
  margin: auto;
  background-color: #FDFCFD;
}
.zoom-enter-active,
.zoom-leave-active {
  -webkit-transition: all .5s;
  transition: all .5s;
}
.evolution-img {
  display: inline;
}
.evolution-img img {
  border: 1px solid black;
}


























































































